import { alpha, useTheme, experimentalStyled as styled } from '@material-ui/core/styles';
import { Box, Grid, Card, Container, Typography, CardContent, CardActions, Button } from '@material-ui/core';
import { varFadeInUp, MotionInView, varFadeInDown } from '../../animate';
import Chip from '@material-ui/core/Chip';
import ArrowRightIcon from '@material-ui/icons/ArrowForward';

export default function SectionThree() {
    return (
        <>
            <Box>
                <Box component="img" src="/static/home/hmsc03_tp_bg.png" className="sc03_tp_img" />
                <Box className="sc03_bg_clr">
                    <Container maxWidth="lg">
                        <Grid container spacing={2} justifyContent="center">
                            <Grid item xs={12}>
                                <MotionInView variants={varFadeInDown}>
                                    <Typography variant="h2" sx={{ textAlign: 'center' }}>
                                    What the Market Says
                                    </Typography>
                                    <Typography component="p" className="sb_ttl_txt" sx={{ mb: {sx: 3, lg: 5 }, }}>
                                        The carbon credit market is emerging as a powerful force in the next decades to come.
                                    </Typography>
                                </MotionInView>
                            </Grid>
                            <Grid item xs={12} md={4}>
                                <MotionInView variants={varFadeInUp}>
                                    <Box className="ccscard">
                                        <Typography component="h1" variant="h1">115%</Typography>
                                        <Typography component="p">Price increase for carbon credits traded in Europe over the past 12 months.</Typography>
                                    </Box>
                                </MotionInView>
                            </Grid>
                            <Grid item xs={12} md={4}>
                                <MotionInView variants={varFadeInUp}>
                                    <Box className="ccscard">
                                        <Typography component="h1" variant="h1">60€</Typography>
                                        <Typography component="p">Per tonne of CO2 in 2020. A low-end estimate for the carbon credit market predictions for 2030.</Typography>
                                    </Box>
                                </MotionInView>
                            </Grid>
                            <Grid item xs={12} md={4}>
                                <MotionInView variants={varFadeInUp}>
                                    <Box className="ccscard">
                                        <Typography component="h1" variant="h1">$2.4bil</Typography>
                                        <Typography component="p">Market size estimate by the end of 2027 according to various economic studies.</Typography>
                                    </Box>
                                </MotionInView>
                            </Grid>
                            
                        </Grid>
                    </Container>
                </Box>
                
                <Box component="img" src="/static/home/hmsc03_btm_bg.png" className="sc03_tp_img" />
            </Box>
        </>
    );
}


