// material
import { experimentalStyled as styled } from '@material-ui/core/styles';
// components
import Page from '../components/Page';
import {
  LandingHero, SectionFour, SectionThree, SectionTwo,
  
} from '../components/_external-pages/carbon-credit';

// ----------------------------------------------------------------------

const RootStyle = styled(Page)(({ theme }) => ({
  height: '100%'
}));

const ContentStyle = styled('div')(({ theme }) => ({
  overflow: 'hidden',
  position: 'relative',
  backgroundColor: theme.palette.background.default
}));

// ----------------------------------------------------------------------

export default function CarbonCredit() {
  return (
    <RootStyle title="Treedefi - The Sustainable yield farm" id="move_top">
      <LandingHero />
      <ContentStyle>
        <SectionTwo />
        <SectionFour />
        <SectionThree />
        
      </ContentStyle>
    </RootStyle>
  );
}
